import React, { useRef, useState, useEffect } from 'react';
import { Helmet } from 'react-helmet';
import './styles/reactComponentNotification.scss';
import Background from './components/background/Background';

import Notification from 'react-simple-notification';

const reactComponentNotification = () => {

  const [title, setTitle] = useState<string>('Amazing title');
  const [content, setContent] = useState<string>('Your description');
  const [icon, setIcon] = useState<string>('<img src="https://cdn-icons-png.flaticon.com/128/2989/2989898.png" />');
  const [closeTime, setCloseTime] = useState<number>(3000);
  const [position, setPosition] = useState<string | null>();

  return (
    <main className="rcn">
      <Helmet>
        <title>React Component Notification</title>
        <meta property="og:title" content="React Component Notification" />
        <meta property="og:description" content="Best portfolio ever seen" />
        <meta property="og:url" content="https://www.balzac.dev/" />
        <meta property="og:site_name" content="Balzac's World ðŸŒ" />
        <meta name="viewport" content="width=device-width, initial-scale=1, maximum-scale=1, user-scalable=no, viewport-fit=cover" />
      </Helmet>
      <Background />
      <Notification
        closeTimeOut={closeTime}
        title={title}
        content={content}
        icon={<img src="https://cdn-icons-png.flaticon.com/128/2989/2989898.png" />}
        className="notiftest"
        position={position || undefined}
      />
      <div className="toolbox">
        <h1>React Component Notification</h1>
        <p>Nb: Notification is automatically displayed when title or content are updated</p>
        <div className="threeParts">
          <input
            type="number"
            placeholder="CloseTimeOut"
            value={closeTime}
            onChange={(e) => setCloseTime(Number(e.target.value))}
          />
          <input
            type="text"
            placeholder="title"
            value={title}
            onChange={(e) => setTitle(e.target.value)}
          />
          <input
            type="text"
            placeholder="content"
            value={content}
            onChange={(e) => setContent(e.target.value)}
          />
        </div>
        <input
          type="text"
          placeholder="icon"
          value={icon}
          onChange={(e) => setIcon(e.target.value)}
        />
        <div className="position">
          <button onClick={() => setPosition('top_left')}>top_left</button>
          <button onClick={() => setPosition('top_center')}>top_center</button>
          <button onClick={() => setPosition('top_right')}>top_right</button>
          <button onClick={() => setPosition('bottom_left')}>bottom_left</button>
          <button onClick={() => setPosition('bottom_center')}>bottom_center</button>
          <button onClick={() => setPosition('bottom_right')}>bottom_right</button>
        </div>
        <button onClick={() => { setTitle(''); setContent('') }} className="close">close</button>
        <code>
          import Notification from 'react-simple-notification';<br />
          <br />
          ...<br />
          <br />
          &lt;Notification<br />
          &nbsp;&nbsp;closeTimeOut=&#123;{closeTime}&#125;<br />
          &nbsp;&nbsp;title="{title}"<br />
          &nbsp;&nbsp;content="{content}"<br />
          &nbsp;&nbsp;icon=&#123;{icon}&#125;<br />
          &nbsp;&nbsp;className="notiftest"<br />
          {position && <code dangerouslySetInnerHTML={{ __html: `&nbsp;&nbsp;position="${position || undefined}<br />` }} />}
          /&gt;
        </code>
        <a href="https://www.npmjs.com/package/react-component-notification">react-component-notification</a>
      </div>
    </main>
  );
};

export default reactComponentNotification;
